<template>
	<view class="page">
		<view class="page-head" :style="'height:'+(l+50)+'px'">
			<image src="../../static/image/home-bg.png" :style="'height:'+(l+50)+'px'" class="page-head-bg"></image>
			<view class="head-title-view" :style="'top:'+(l+10)+'px'">
				<view class="head-title-wrapper">
					<text class="head-title">配货中心</text>
				</view>
			</view>
		</view>
		<scroll-view class="content-container" scroll-y>
			<view class="goods-list-view">
				<view v-for="(item,index) in goodsList" :key="index" class="goods-wrapper" @tap="linkDetail(item.id)">
					<view>
						<image class="goods-image" :src="item.thumbnail"></image>
					</view>
					<view class="goods-info">
						<text>{{item.name}}</text>
					</view>
					<view class="goods-bottom">
						<text class="goods-price">{{item.price}}</text>
						<image @tap.stop="addStorage" class="add-icon" src="../../static/icon/icon-addd.png">
						</image>
					</view>
				</view>
			</view>
		</scroll-view>
		<view class="sto-view" @tap="linkSto()">
			<view class="sto-wrapper">
				<image class="sto-image" src="../../static/icon/24gl-bag.png"></image>
				<view class="sto-num">5</view>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		goods_list
	} from "../../api/home.js"
	export default {
		data() {
			return {
				l: 0,
				goodsList: []
			}
		},
		onLoad() {
			let info = uni.getSystemInfoSync();
			this.l = info.statusBarHeight;
			goods_list().then(res => {
				this.goodsList = res.list;
			})
		},
		methods: {
			linkDetail(id) {
				uni.navigateTo({
					url: "../goods/detail?id=" + id
				})
			},
			addStorage() {
				getApp().globalData.toast("添加成功");
			},
			linkSto() {
				uni.navigateTo({
					url: "../sto/sto"
				})
			}
		}
	}
</script>

<style>
	.sto-wrapper {
		width: 100rpx;
		height: 100rpx;
		border-radius: 100rpx;
		display: flex;
		justify-content: center;
		align-items: center;
		border: 1rpx solid #515151;
		position: relative;
	}

	.sto-num {
		background-color: #ff3333;
		color: #FFFFFF;
		height: 50rpx;
		min-width: 50rpx;
		border-radius: 50rpx;
		position: absolute;
		display: flex;
		justify-content: center;
		align-items: center;
		top: -25rpx;
		right: 0;
	}

	.sto-image {
		width: 70rpx;
		height: 70rpx;
	}

	.sto-view {
		position: fixed;
		bottom: 200rpx;
		right: 50rpx;
	}

	.page-head-bg {
		width: 100%;
	}

	.page-head {
		position: relative;
	}

	.head-title-view {
		position: absolute;
		bottom: 10px;
		color: #FFFFFF;
		padding-left: 15px;
		box-sizing: border-box;
	}

	.head-title {
		font-weight: 900;
		font-size: 45rpx;
	}

	.head-sub-title {
		margin-left: 10rpx;
		font-weight: 800;
		font-size: 30rpx;
	}

	.head-title-wrapper {
		display: flex;
		align-items: center;
	}

	.content-container {
		background-color: #F8F8F8;
		flex: 1;
		height: 0;
	}


	.goods-list-view {
		width: 100%;
		background-color: #FFFFFF;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		border-top-left-radius: 30rpx;
		border-top-right-radius: 30rpx;
		background-color: #f7f7f7;
		padding: 10rpx 30rpx;
		box-sizing: border-box;
	}

	.goods-wrapper {
		width: 335rpx;
		border-radius: 15rpx;
		margin-top: 20rpx;
		padding-bottom: 20rpx;
		background-color: #FFFFFF;
		overflow: hidden;
	}

	.goods-image {
		width: 335rpx;
		height: 335rpx;
	}

	.goods-info {
		color: #000000;
		font-weight: bold;
		font-size: 30rpx;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
		padding: 0 20rpx;
		box-sizing: border-box;
		word-break: break-all;
		word-wrap: break-word;
		margin-top: 15rpx;
	}

	.goods-price {
		color: #FF3333;
		font-size: 40rpx;
		font-weight: 900;
		box-sizing: border-box;
	}

	.goods-price::before {
		content: '¥';
		font-size: 24rpx;
		font-weight: normal;
	}

	.add-icon {
		width: 40rpx;
		height: 40rpx;
	}

	.goods-bottom {
		display: flex;
		justify-content: space-between;
		margin-top: 30rpx;
		padding: 0 20rpx;
	}
</style>
